import React from "react";
import css from './style.module.css';
const Order = (props) => {

     
return <div className={css.Order}>


     <p>
      :   : {props.order.orts.Bacon},  : {props.order.orts.Cheese},   : {props.order.orts.Meat}, : {props.order.orts.Salad}</p>
     <p> : {props.order.hayag.name} | {props.order.hayag.street} | {props.order.hayag.city}</p>

     <p> : <strong>{props.order.dun}</strong></p>
     
</div>;



};



export default Order;